.form-wrapper{
	padding: 15px;
	padding-bottom: 35px;
	background: rgba($light-grey,.8);
	border-radius: 10px;
	width: 350px;
	h3{
		color: $blood-red;
		font-weight: bold;
		line-height: 1;
		font-size: 33px;
		text-align: center;
		span{
			text-transform: uppercase;
			display: block;
			color: $text;
			font-size: 20px;
			margin-bottom: 5px;
			font-weight: bold;
		}
	}
}

.form-with-shadow{
	box-shadow: 0 2px 1px 0 rgba(0,0,0,.4)
}

form{
	border: 2px solid #fff;
	border-radius: 10px;
	background-color: $light-grey;
	color: $text;
	text-align: right;
	padding: 10px;
	padding-bottom: 0;
	label{
		margin-top: 10px;
		display: inline-block;
		font-size: 20px;
		span{
			margin-right: 5px;
		}
	}
	input[type="text"]{
		width: 250px;
		border: 1px solid #B4B2B2;
		border-radius: 5px;
		background: #fff;
		padding: 8px 5px;
		font-size: 20px;
		color: $text;
		margin-right: 15px;
	}
	button[type="submit"]{
		margin-left: 50%;
		transform: translate(-50%, 20px);
	}
}