/*==========  Desktop First  ==========*/
// ≥768px
@media (max-width: 767.98px){
	.show-sm{
		display: block;
	}
	.hide-sm{
		display: none;
	}
	.container{
		width: 720px;
		margin: 0 auto;
	}
	header{
		.row{
			width: 720px;
		}
		.adress{
			line-height: 1.4;
		}
	}
	.hero{
		&-form{
			left: 50%;
			transform: translate(-50%,0);
			width: 480px;
		}
	}


	// 
	// 
	.testimonials{
		display: none;
	}
}

@media (min-width: 768px){
	.show-sm{
		display: none;
	}
}